import React from 'react';

export const codePreprocess = function (txt) {
    return txt.replaceAll("PropTypes", "<props>PropTypes</props>")
        .replaceAll("func", "<primpt>func</primpt>")
        .replaceAll("object", "<primpt>object</primpt>")
        .replaceAll("string", "<primpt>string</primpt>")
        .replaceAll("number", "<primpt>number</primpt>")
        .replaceAll("any", "<objpt>any</objpt>")
        .replaceAll("shape", "<objpt>shape</objpt>")
        .replaceAll("oneOf", "<objpt>oneOf</objpt>")
        .replaceAll("array", "<objpt>array</objpt>")
        .replaceAll("\",", "'</string>,")
        .replaceAll("\"\n", "'</string>\n")
        .replaceAll("\"]", "'</string>]")
        .replaceAll("\"", "<string>'");
}

