import React, { Component, Fragment } from 'react';
import { Button, Alert, Table, Badge } from 'reactstrap';
import { JOFGENGenerator, JOFGENDesigner } from '../../lib';
import { codePreprocess } from './preprocessing';
import Icon from '@mdi/react';
import { mdiAlertRhombus, mdiCheckCircle, mdiCloseCircle, mdiCircleHalf, mdiInformation, mdiAlertCircle, mdiShare } from '@mdi/js';
import { local_urls } from '../../urls';

const generatorProps = `JOFGENDesigner.propTypes = {\n    onChange: PropTypes.func,\n    \n    <comment>// JSON object used for generating designer</comment>\n    json: PropTypes.array,\n    \n    <comment>// structure of returned value object</comment>\n    mode: PropTypes.oneOf(["tree", "linear", "linear_merge"]),\n    size: PropTypes.string,\n    \n    <comment>// parameters of generic id. Generic id is used when unique id is:\n    // - not defined by user\n    // - is lost\n    // - is invalid</comment>\n    jkey: PropTypes.shape({\n        prefix: PropTypes.string.isRequired,\n        sufix: PropTypes.string.isRequired\n    }),\n    \n    <comment>// this text and icon shows when json object is not valid</comment>\n    fatal_error_msg: PropTypes.string.isRequired,\n    fatal_error_icon: PropTypes.any.isRequired,\n    \n    extended: PropTypes.bool,\n    export: PropTypes.bool,\n    hideDissabled: PropTypes.bool,\n    translation: PropTypes.shape(translationPropType),\n    icons: PropTypes.shape(iconPropType)\n};`;
const examples = [
    `<import>import</import> <classcmd>React</classcmd>, { <classcmd>Component</classcmd>, <classcmd>Fragment</classcmd> } from <path>'react'</path>;\n<import>import</import> { <classcmd>JOFGENDesigner</classcmd> } from <path>'json-obj-form-generator'</path>;\n<import>import</import> <path>'bootstrap/dist/css/bootstrap.css'</path>;\n<import>import</import> <path>'json-obj-form-generator/dist/css/designer.css'</path>;\n\n<cmd>export default class</cmd> <classcmd>DesignerExample1</classcmd> extends <classcmd>Component</classcmd> {\n    <cmd>constructor</cmd>(<prm>props</prm>) {\n        <cmd>super</cmd>(<prm>props</prm>);\n        <cmd>this</cmd>.<prm>state</prm> = {\n            json: [\n                {\n                    "uid":"unic_old",\n                    "name":"How old is your unicorn?",\n                    "required":true,\n                    "type":"int"\n                },\n                {\n                    "uid":"unic_color",\n                    "name":"What's the best color of unicorn?",\n                    "tip":"Try to use this tip: https://youtu.be/5phx8eycHZ4",\n                    "type":"color"\n                }\n            ]\n        };\n    }\n    \n    <primpt>render</primpt>()\n    {\n        <ret>return</ret> <<classcmd>JOFGENDesigner</classcmd> json={this.state.json} />\n    }\n};\n`
]

export default class Designer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            json: [
                {
                    "uid": "unic_old",
                    "name": "How old is your unicorn?",
                    "required": true,
                    "type": "int"
                },
                {
                    "uid": "unic_color",
                    "name": "What's the best color of unicorn?",
                    "tip": "Try to use this tip: https://youtu.be/5phx8eycHZ4",
                    "type": "color"
                }
            ]
        };
    }

    render() {
        return (
            <Fragment>
                <section>
                    <h2>
                        Designer
                    </h2>
                    <playground>
                        <code dangerouslySetInnerHTML={{ __html: examples[0] }}>
                        </code>
                        <compiled>
                            {<JOFGENDesigner json={this.state.json} />}
                        </compiled>
                    </playground>
                </section>

                <section id="properties">
                    <h3>
                        Properties
                    </h3>
                    <playground>
                        <code className="norm-border-radius" dangerouslySetInnerHTML={{ __html: codePreprocess(generatorProps) }}>
                        </code>
                    </playground>
                </section>
                <section id="parameters">
                    <h3>
                        Parameters
                    </h3>
                    <Alert color="info">
                        <b><Icon path={mdiInformation} size={0.9} color="#26c6da" className="mr-1 mb-1" /> Traslations and icons</b><br />
                        <div style={{ color: "#777777" }}>
                            As you can see, some parameters - like <prm>translation</prm> or <prm>icons</prm> are not described right here, due to
                            better clarity of documentation. You can find them described in their own sections.<br />
                            All texts and icons used in designer are fully customizable. So you can translate designer (into different languages)
                            and adjust it to your own ideas / style.
                        </div>
                    </Alert>


                    <h4 id="parameter-export">
                        export
                    </h4>
                    <p>
                        If parameter <prm>export</prm> is set to <string>true</string>, designer will show special button <Button color="primary" size="sm" className="mr-1 ml-1"><svg className="jofgen-D-icon jofgen-D-icon-export" viewBox="0 0 24 24"><path d="M23,12L19,8V11H10V13H19V16M1,18V6C1,4.89 1.9,4 3,4H15A2,2 0 0,1 17,6V9H15V6H3V18H15V15H17V18A2,2 0 0,1 15,20H3A2,2 0 0,1 1,18Z"></path></svg>Export JSON</Button> on top.
                        This button allows user to copy <prm>json</prm> object to clipboard. By default is value of this parameter set to <string>false</string>.
                        <playground>
                            <code dangerouslySetInnerHTML={{
                                __html: `<<classcmd>JOFGENDesigner</classcmd> export={true|false} />`
                            }}>
                            </code>
                            <compiled className="norm-border-radius">
                                <JOFGENDesigner export={true} json={[{ "uid": "unicorn", "name": "unicorn", "value": "#ffffff", "type": "color" }]} />
                            </compiled>
                        </playground>
                    </p>

                    <h4 id="parameter-extended">
                        extended
                    </h4>
                    <p>
                        By default is value of this parameter set to <string>true</string>. If parameter is set to <string>true</string>, designer will show button <svg className="mr-1" style={{ width: "20px", height: "20px" }} viewBox="0 0 24 24"><path d="M2,5.27L3.28,4L20,20.72L18.73,22L15.65,18.92C14.5,19.3 13.28,19.5 12,19.5C7,19.5 2.73,16.39 1,12C1.69,10.24 2.79,8.69 4.19,7.46L2,5.27M12,9A3,3 0 0,1 15,12C15,12.35 14.94,12.69 14.83,13L11,9.17C11.31,9.06 11.65,9 12,9M12,4.5C17,4.5 21.27,7.61 23,12C22.18,14.08 20.79,15.88 19,17.19L17.58,15.76C18.94,14.82 20.06,13.54 20.82,12C19.17,8.64 15.76,6.5 12,6.5C10.91,6.5 9.84,6.68 8.84,7L7.3,5.47C8.74,4.85 10.33,4.5 12,4.5M3.18,12C4.83,15.36 8.24,17.5 12,17.5C12.69,17.5 13.37,17.43 14,17.29L11.72,15C10.29,14.85 9.15,13.71 9,12.28L5.6,8.87C4.61,9.72 3.78,10.78 3.18,12Z"></path></svg> 
                        on left panel of each element and button <Button size="sm" className="mr-1 ml-1" color="success"><svg className="jofgen-D-icon jofgen-D-icon-preview" viewBox="0 0 24 24"><path d="M17,18C17.56,18 18,18.44 18,19C18,19.56 17.56,20 17,20C16.44,20 16,19.56 16,19C16,18.44 16.44,18 17,18M17,15C14.27,15 11.94,16.66 11,19C11.94,21.34 14.27,23 17,23C19.73,23 22.06,21.34 23,19C22.06,16.66 19.73,15 17,15M17,21.5A2.5,2.5 0 0,1 14.5,19A2.5,2.5 0 0,1 17,16.5A2.5,2.5 0 0,1 19.5,19A2.5,2.5 0 0,1 17,21.5M9.27,20H6V4H13V9H18V13.07C18.7,13.15 19.36,13.32 20,13.56V8L14,2H6A2,2 0 0,0 4,4V20A2,2 0 0,0 6,22H10.5C10,21.41 9.59,20.73 9.27,20Z"></path></svg>Form preview</Button> on top right corner of designer. When you click on this icon, it changes to <svg className="mr-1" style={{ width: "20px", height: "20px" }} viewBox="0 0 24 24"><path d="M12,9A3,3 0 0,1 15,12A3,3 0 0,1 12,15A3,3 0 0,1 9,12A3,3 0 0,1 12,9M12,4.5C17,4.5 21.27,7.61 23,12C21.27,16.39 17,19.5 12,19.5C7,19.5 2.73,16.39 1,12C2.73,7.61 7,4.5 12,4.5M3.18,12C4.83,15.36 8.24,17.5 12,17.5C15.76,17.5 19.17,15.36 20.82,12C19.17,8.64 15.76,6.5 12,6.5C8.24,6.5 4.83,8.64 3.18,12Z."></path></svg> 
                        and on right side you will see what the element will look like when generated. This feature allows you to test your elements or whole 
                        form (if you will click on button <Button size="sm" className="mr-1 ml-1" color="success"><svg className="jofgen-D-icon jofgen-D-icon-preview" viewBox="0 0 24 24"><path d="M17,18C17.56,18 18,18.44 18,19C18,19.56 17.56,20 17,20C16.44,20 16,19.56 16,19C16,18.44 16.44,18 17,18M17,15C14.27,15 11.94,16.66 11,19C11.94,21.34 14.27,23 17,23C19.73,23 22.06,21.34 23,19C22.06,16.66 19.73,15 17,15M17,21.5A2.5,2.5 0 0,1 14.5,19A2.5,2.5 0 0,1 17,16.5A2.5,2.5 0 0,1 19.5,19A2.5,2.5 0 0,1 17,21.5M9.27,20H6V4H13V9H18V13.07C18.7,13.15 19.36,13.32 20,13.56V8L14,2H6A2,2 0 0,0 4,4V20A2,2 0 0,0 6,22H10.5C10,21.41 9.59,20.73 9.27,20Z"></path></svg>Form preview</Button> or root element).<br />
                        If parameter is set to <string>false</string>, all these buttons stays hidden, as you can see bellow.         
                        
                        <playground>
                            <code dangerouslySetInnerHTML={{
                                __html: `<<classcmd>JOFGENDesigner</classcmd> extended={false} />`
                            }}>
                            </code>
                            <compiled className="norm-border-radius">
                                <JOFGENDesigner extended={false} json={[{ "uid": "unicorn", "name": "unicorn", "value": "#ffffff", "type": "color" }]} />
                            </compiled>
                        </playground>
                    </p>

                    <h4 id="parameter-hideDissabled">
                        hideDissabled
                    </h4>
                    <p>
                        This parameter was added for cases when you need to load form with data filled by user. For example, if you stores values from form in database and want to reload it.
                        Object can have different structure depending on selected <prm>mode</prm>.
                        <playground>
                            <code dangerouslySetInnerHTML={{
                                __html: `<<classcmd>JOFGENDesigner</classcmd> hideDissabled={false} />`
                            }}>
                            </code>
                            <compiled className="norm-border-radius">
                                <JOFGENDesigner hideDissabled={true} json={[{ "uid": "unicorn", "name": "unicorn", "value": "#ffffff", "type": "color" }]} />
                            </compiled>
                        </playground>
                    </p>

                    <h4 id="parameter-json">
                        json
                    </h4>
                    <p>
                        This parameter can be used for changing <prm>json</prm> which was created using designer or <a href={`${local_urls["version_prefix_router"]}/components/codesheet`}>codesheet</a> - for example, if you are storing your <prm>json</prm> to database.<br />
                        Rules applied during JSON object creation depends on selected <prm>mode</prm>.
                        <Alert color="warning" className="mt-3 mb-4">
                            <div className="pt-1 pb-2">
                                <b><Icon path={mdiAlertRhombus} size={0.9} color="#f3a000" className="mr-1 mb-1" /> Compatibility between modes:</b><br />
                                Compatibility of object created in designer with generator.
                            </div>
                            <Table bordered>
                                <thead>
                                    <tr>
                                        <td colSpan="2" rowSpan="2" className="no-border" ></td>
                                        <td colSpan="3" className="text-center table-lvl-1">
                                            <b>
                                                Generator mode
                                            </b>
                                        </td>
                                    </tr>
                                    <tr className="table-lvl-2">
                                        <td className="text-center">
                                            linear
                                            </td>
                                        <td className="text-center">
                                            linear_merge
                                            </td>
                                        <td className="text-center">
                                            tree
                                            </td>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td rowSpan="3" className="text-center vertical-center table-lvl-1">
                                            <b>
                                                Designer mode
                                            </b>
                                        </td>
                                        <td className="text-center table-lvl-2">linear</td>
                                        <td className="text-center"><Icon path={mdiCheckCircle} size={0.9} color="#8bc34a" className="mr-1 mb-1" /></td>
                                        <td className="text-center"><Icon path={mdiCheckCircle} size={0.9} color="#8bc34a" className="mr-1 mb-1" /></td>
                                        <td className="text-center"><Icon path={mdiCheckCircle} size={0.9} color="#8bc34a" className="mr-1 mb-1" /></td>
                                    </tr>
                                    <tr>
                                        <td className="text-center table-lvl-2">linear_merge</td>
                                        <td className="text-center"><Icon path={mdiCircleHalf} size={0.9} color="#ffeb3b" className="mr-1 mb-1" /></td>
                                        <td className="text-center"><Icon path={mdiCheckCircle} size={0.9} color="#8bc34a" className="mr-1 mb-1" /></td>
                                        <td className="text-center"><Icon path={mdiCheckCircle} size={0.9} color="#8bc34a" className="mr-1 mb-1" /></td>
                                    </tr>
                                    <tr>
                                        <td className="text-center table-lvl-2">tree</td>
                                        <td className="text-center"><Icon path={mdiCircleHalf} size={0.9} color="#ffeb3b" className="mr-1 mb-1" /></td>
                                        <td className="text-center"><Icon path={mdiCheckCircle} size={0.9} color="#8bc34a" className="mr-1 mb-1" /></td>
                                        <td className="text-center"><Icon path={mdiCheckCircle} size={0.9} color="#8bc34a" className="mr-1 mb-1" /></td>
                                    </tr>
                                </tbody>
                            </Table>
                            <div style={{ color: "#777777" }}>
                                <Icon path={mdiCheckCircle} size={0.9} color="#8bc34a" className="mr-1 mb-1" /> - fully compatible (always generated without errors)<br />
                                <Icon path={mdiCircleHalf} size={0.9} color="#ffeb3b" className="mr-1 mb-1" /> -  error occurs in some cases (sometimes may be generated with errors)<br />
                                <Icon path={mdiCloseCircle} size={0.9} color="#f44336" className="mr-1 mb-1" /> - error occurs in all cases (always generated with error)<br />
                            </div>

                        </Alert>

                    </p>

                    <h4 id="parameter-mode">
                        mode
                    </h4>
                    <p>
                        One of the key parameters is <prm>mode</prm>. It is set to <string>'linear'</string> by default. Using this parameter you can change
                        structure of value object returned by generator. Changing value of this parameter will affect validation of generated <prm>json</prm>,
                        because each mode uses its own validation logic during checking the uniqueness of the id.<br />
                        As first example <prm>json</prm> we will use this simple object:
                        <playground>
                            <code dangerouslySetInnerHTML={{ __html: `<cmd>const</cmd> <prm>json</prm> = [\n    {\n        "uid": "parent",\n        "name": "Parent",\n        "type": "sec",\n        "sub": [\n            {\n                "uid": "sub",\n                "name": "Sub",\n                "type": "sec",\n                "sub": [\n                    {\n                        "uid": "unicorn",\n                        "name": "unicorn",\n                        "value": "#ffffff",\n                        "type": "color"\n                    }\n                ]\n            }\n        ]\n    },\n    {\n        "uid": "parent1",\n        "name": "Parent1",\n        "type": "sec",\n        "sub": [\n            {\n                "uid": "sub1",\n                "name": "Sub1",\n                "type": "sec",\n                "sub": [\n                    {\n                        "uid": "dragon",\n                        "name": "dragon",\n                        "value": "#ffffff",\n                        "type": "color"\n                    }\n                ]\n            }\n        ]\n    }\n]` }}>
                            </code>
                        </playground>

                        Value returned after filling generated form will be:
                        <ul>
                            <li>
                                <b><string>'linear'</string></b><br />
                                After filling form generated using <classcmd>JOFGENGenerator</classcmd>, we will get object like this:
                                <playground>
                                    <code className="norm-border-radius" dangerouslySetInnerHTML={{
                                        __html: `{\n    <string>'unicorn'</string>: <string>'#ffffff'</string>,\n    <string>'dragon'</string>: <string>'#ffffff'</string>\n}`
                                    }}>
                                    </code>
                                </playground>
                                We can see also generated designer here:
                                <playground>
                                    <code dangerouslySetInnerHTML={{
                                        __html: `<<classcmd>JOFGENDesigner</classcmd> mode=<string>'linear'</string> json={this.state.json} />`
                                    }}>
                                    </code>
                                    <compiled className="norm-border-radius">
                                        <JOFGENDesigner mode="linear" json={[{ "uid": "parent", "name": "Parent", "type": "sec", "sub": [{ "uid": "sub", "name": "Sub", "type": "sec", "sub": [{ "uid": "unicorn", "name": "unicorn", "value": "#ffffff", "type": "color" }] }] }, { "uid": "parent1", "name": "Parent1", "type": "sec", "sub": [{ "uid": "sub1", "name": "Sub1", "type": "sec", "sub": [{ "uid": "unicorn", "name": "unicorn", "value": "#ffffff", "type": "color" }] }] }]} />
                                    </compiled>
                                </playground>
                                As we can see some <prm>uid</prm>s are not valid.
                                Let's try to change <prm>uid</prm> from <string>'dragon'</string> to <string>'unicorn'</string>. Designer will show error alert.
                                Because filling form after generating it, may cause data loss or data corruption in some cases.<br />
                                <playground>
                                    <code className="norm-border-radius" dangerouslySetInnerHTML={{
                                        __html: `{\n    <string class='text-primary'>'unicorn'</string>: <string class='text-primary'>'#ffffff'</string>,\n    <string class='text-danger'>'unicorn'</string>: <string class='text-danger'>'#000000'</string>\n}`
                                    }}>
                                    </code>
                                </playground>
                                <Alert color="danger">
                                    <b><Icon path={mdiAlertCircle} size={0.9} color="#ff5252" className="mr-1 mb-1" /> Data loss</b>
                                    <div>As you can see: value will be set to value from last changed input with this id, so another value will be lost.</div>
                                </Alert>
                                We can make this example little bit more special by changing the type of one variable from <string>'color'</string> to for example: <string>'integer'</string>.
                                There we can see data corruption and data loss in one case, which can affect your application.
                                <playground>
                                    <code className="norm-border-radius" dangerouslySetInnerHTML={{
                                        __html: `{\n    <string class='text-primary'>'unicorn'</string>: <string class='text-primary'>'#ffffff'</string>,\n    <string class='text-danger'>'unicorn'</string>: <val class='text-danger'>32</val>\n}`
                                    }}>
                                    </code>
                                </playground>
                                <Alert color="danger">
                                    <b><Icon path={mdiAlertCircle} size={0.9} color="#ff5252" className="mr-1 mb-1" /> Data loss and data corruption</b>
                                    <div>
                                        As you can see in this case, value will be set to value from last changed input with this id, so another value will be lost - like in previous example.
                                        But there is also another problem - conflict of data types.
                                    </div>
                                </Alert>
                                <Alert color="info">
                                    <b><Icon path={mdiInformation} size={0.9} color="#26c6da" className="mr-1 mb-1" /> Note</b><br />
                                    <div style={{ color: "#777777" }}>
                                        All mentioned error cases will result in the displaying of warning messages to the user. You can change translation of this messages,
                                        using parameter <prm>translation</prm>.
                                    </div>
                                </Alert>
                            </li>
                            <li>
                                <b><string>'linear_merge'</string></b><br />
                                <playground>
                                    <code className="norm-border-radius" dangerouslySetInnerHTML={{
                                        __html: `{\n    <string>'parent:sub:unicorn'</string>: <string>'#ffffff'</string>\n    <string>'parent1:sub1:dragon'</string>: <string>'#ffffff'</string>\n}`
                                    }}>
                                    </code>
                                </playground>
                                Let's try to change <prm>uid</prm> from <string>'dragon'</string> to <string>'unicorn'</string>. Designer will be generated without errors, because keys of values will be different (unique).
                                We can see result here:
                                <playground>
                                    <code dangerouslySetInnerHTML={{
                                        __html: `<<classcmd>JOFGENDesigner</classcmd> mode=<string>'linear_merge'</string> json={this.state.json} />`
                                    }}>
                                    </code>
                                    <compiled className="norm-border-radius">
                                        <JOFGENDesigner mode="linear_merge" json={[{ "uid": "parent", "name": "Parent", "type": "sec", "sub": [{ "uid": "sub", "name": "Sub", "type": "sec", "sub": [{ "uid": "unicorn", "name": "unicorn", "value": "#ffffff", "type": "color" }] }] }, { "uid": "parent1", "name": "Parent1", "type": "sec", "sub": [{ "uid": "sub1", "name": "Sub1", "type": "sec", "sub": [{ "uid": "unicorn", "name": "unicorn", "value": "#ffffff", "type": "color" }] }] }]} />
                                    </compiled>
                                </playground>
                            </li>
                            <li>
                                <b><string>'tree'</string></b><br />
                                Let's try to change <prm>uid</prm> from <string>'dragon'</string> to <string>'unicorn'</string>. Designer will be generated without errors, because keys of values will be different (unique)
                                like in previous case. We can see result here:
                                <playground>
                                    <code className="norm-border-radius" dangerouslySetInnerHTML={{
                                        __html: `{\n    <string>'parent'</string>: {\n        <string>'sub'</string>: {\n            <string>'unicorn'</string>: <string>'#ffffff'</string>\n        }\n    },\n    <string>'parent1'</string>: {\n        <string>'sub1'</string>: {\n            <string>'dragon'</string>: <string>'#ffffff'</string>\n        }\n    }\n}`
                                    }}>
                                    </code>
                                </playground>
                                As we can see: result object copies structure of input parameter <prm>json</prm>.<br />
                                Let's try to change <prm>uid</prm> from <string>'dragon'</string> to <string>'unicorn'</string>. Designer will be generated without errors. We can see result here:
                                because keys of values will be different.
                                <playground>
                                    <code dangerouslySetInnerHTML={{
                                        __html: `<<classcmd>JOFGENDesigner</classcmd> mode=<string>'tree'</string> json={this.state.json} />`
                                    }}>
                                    </code>
                                    <compiled className="norm-border-radius">
                                        <JOFGENDesigner mode="tree" json={[{ "uid": "parent", "name": "Parent", "type": "sec", "sub": [{ "uid": "sub", "name": "Sub", "type": "sec", "sub": [{ "uid": "unicorn", "name": "unicorn", "value": "#ffffff", "type": "color" }] }] }, { "uid": "parent1", "name": "Parent1", "type": "sec", "sub": [{ "uid": "sub1", "name": "Sub1", "type": "sec", "sub": [{ "uid": "unicorn", "name": "unicorn", "value": "#ffffff", "type": "color" }] }] }]} />
                                    </compiled>
                                </playground>
                            </li>
                        </ul>
                    </p>

                    <h4 id="parameter-size">
                        size
                    </h4>
                    <p>
                        Size of text, inputs, dropdowns and buttons. Value of this parameter can be <string>'sm'</string> or <string>'lg'</string>.
                    </p>

                    <h4 id="parameter-title">
                        title
                    </h4>
                    <p>
                        Using this parameter you can change also title of designer.
                    </p>
                </section>
                <section id="methods">
                    <h3>
                        Methods
                    </h3>

                    <h4 id="parameter-json">
                        onChange(json, valid)
                    </h4>
                    <p>
                        Method takes parameter <prm>json</prm>, which represents object, that can be used for generating form using <classcmd>JOFGENGenerator</classcmd>. State and speed of <prm>json</prm> validation 
                        depends on selected <prm>mode</prm> and depth of <prm>json</prm> object, that declares structure of generated form. Second parameter <prm>valid</prm> is <string>true</string>, if returned <prm>json</prm> is
                        valid, otherwise value of <prm>valid</prm> will be <string>false</string>.
                    </p>                    
                </section>
            </Fragment>
        );

    }
}